<template>
     <ul class="catalog__pagination pagination">
          <li class="pagination__item" >
            <a class="pagination__link pagination__link--arrow" href="#" aria-label="Предыдущая страница" @click.prevent="paginateLate">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </a>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
            <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click.prevent="paginate(pageNumber)">
              {{ pageNumber }}
            </a>
          </li>
 
          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница" @click.prevent="paginateNext" >
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </a>
          </li>
        </ul>
</template>

<script>
export default {
    model: {
      prop: "page",
      event: "paginate",
    },
    props: ['page', 'count', 'perPage'],
    computed: {
      pages() {
        return Math.ceil(this.count / this.perPage);
      }
    },
    methods: {
      paginate(page) {
        this.$emit("paginate", page)
        $event.preventDefault()
      },
      paginateNext() {
        if (this.page != Math.ceil(this.count / this.perPage))
        this.$emit("paginate", this.page + 1)
        }, 
      paginateLate() {
        if (this.page != 1)
        this.$emit("paginate", this.page - 1)
    }
  }
}

</script>

<!-- :class="{'pagination__link--disabled': currentPage = 1}" -->