<template>
  <main class="content container" v-if="cartLoading">Загрузка корзины</main>
<main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info" v-if="$store.state.cartProducts.length <= 0">
        Вы ничего не положили в корзину
      </span>
      <span class="content__info" v-if="$store.state.cartProducts.length > 0">
        {{ $store.state.cartProducts.length }} товар(а)
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormating }} ₽</span>
          </p>

          <router-link tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit" :disabled="$store.state.cartProducts.length <= 0">
            Оформить заказ
          </router-link>

        </div>
      </form>
    </section>
  </main>
</template>

<script>
import CartItem from '@/components/CartItem.vue';
import numberFormating from '@/helpers/numberFormating';
import axios from 'axios';
import { mapGetters } from 'vuex'
import { API_BASE_URL } from '@/config';
import router from '@/router';

export default {
  data(){
    return {
      cartLoading: false,

      cartData: null,
    }
  },
  components: { CartItem, router },
    filters: {numberFormating},
    computed: {
      ...mapGetters({products: 'cartDetail', totalPrice: 'cartTotalPrice'}),
    },
    methods: {
      loadCart() {
          this.cartLoading = true;
          axios.get(API_BASE_URL + '/api/baskets')
          .then(response => this.cartData = response.data)
          .then(this.cartLoading = false)
        },
    },
    created(){
      this.loadCart();
    }

}
</script>