<template>
    <div v-if="cartLoading"><img src="./Settings.gif" alt=""></div>
     <router-link v-else class="header__cart" aria-label="Корзина с товарами" :to="{name: 'cart'}">
        <svg width="30" height="21" fill="currentColor">
          <use xlink:href="#icon-cart"></use>
        </svg>
        <span class="header__count" aria-label="Количество товаров">{{ $store.state.cartProducts.length }}</span>
      </router-link>  
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';


export default {
data(){
  return{
    cartLoading: false
  }
},
methods: {
      loadCart() {
          this.cartLoading = true;
          console.log(this.cartLoading)
          axios.get(API_BASE_URL + '/api/baskets')
          .then(response => this.cartData = response.data)
          .then(this.cartLoading = false)
          console.log(this.cartLoading)
          
        },
    },
    created(){
      this.loadCart();
    }
}
</script>